/* Стиль вкладки */
.tab {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 341px;
  overflow: hidden;
  padding-bottom: 2px;
  border-bottom: 2px solid rgba(120, 120, 120, 0.2);
  margin-bottom: 40px;
}
@media (max-width: 1199px) {
  .tab {
    margin-bottom: 45px;
    width: 332px;
  }
}
@media (max-width: 767px) {
  .tab {
    width: 287px;
    margin-bottom: 35px;
  }
}

/* Стиль кнопок, которые используются для открытия содержимого вкладки */
.tab button {
  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 0;
  padding-bottom: 11px;
  transition: 0.3s;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: rgba(16, 38, 84, 0.5);
}
@media (max-width: 1199px) {
  .tab button {
    font-size: 18px;
    line-height: 27px;
    padding-bottom: 12px;
    padding-top: 18px;
  }
}
@media (max-width: 767px) {
  .tab button {
    font-size: 16px;
    line-height: 24px;
    padding-top: 22px;
    padding-bottom: 11px;
    padding-left: 2px;
    padding-right: 2px;
  }
}
@media (hover: hover) {
  .tab button:hover:not(.focus-visible) {
    color: #102654;
  }
}
.tab button.focus-visible:focus {
  color: #102654;
}

/* Создание активного/текущего класса связи вкладки */
.tab button.active {
  box-shadow: 0 2px #d10a42;
  color: #102654;
}

