.coachs {
  & h2 {
    font-family: 'Alumni Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 54px;
    line-height: 65px;
    letter-spacing: 0.03em;

    @include vp-1199 {
      font-size: 42px;
      line-height: 50px;
      text-align: center;
    }

    @include vp-767 {
      font-size: 32px;
      line-height: 38px;
    }
  }


}

.coachs__container {
  max-width: 1160px;
  padding: 0;
}

